@import "../../../css-config/mixins.scss";

.shop-by-category {
  margin: 25px 0;
  @include md {
    margin: 50px 0;
  }
  .categories {
    display: flex;
    flex-flow: wrap;
    gap: 10px;
    .category {
      background-color: #000;
      width: calc(50% - 5px);
      cursor: pointer;
      overflow: hidden;
      border-radius: 10px;
      @include md {
        width: calc(25% - 10px);
      }
      img {
        width: 100%;
        display: block;
        transform: all ease 0.5s;
      }
      &:hover {
        img {
          transform: scale(1.1);
        }
      }
    }
  }
}
